.App {
  height: 100vh;
  background-color: #0E1933;
  background: -webkit-gradient(linear, left top, left bottom, from(#0E1933), to(#CA9D8E));
  background: -webkit-linear-gradient(top, #0E1933, #CA9D8E);
  background: -o-linear-gradient(top, #0E1933, #CA9D8E);
  background: linear-gradient(180deg, #0E1933, #CA9D8E);
  background-size: 400% 400%;
  -webkit-animation: AnimateGradient 20s ease infinite;
  animation: AnimateGradient 20s ease infinite;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center; }

@-webkit-keyframes AnimateGradient {
  0% {
    background-position: 0% 5%; }
  50% {
    background-position: 100% 95%; }
  100% {
    background-position: 0% 5%; } }

@keyframes AnimateGradient {
  0% {
    background-position: 0% 5%; }
  50% {
    background-position: 100% 95%; }
  100% {
    background-position: 0% 5%; } }



